import "./login.scss";
import { NavLink } from "react-router-dom";

const Login = () => {
    return (
        <div className="login">
            <div className="top">
                <div className="wrapper">
                    <img className="logo" src="https://upload.wikimedia.org/wikipedia/commons/thumb/0/08/Netflix_2015_logo.svg/2560px-Netflix_2015_logo.svg.png" alt="" />
                </div>
            </div>
            <div className="container">
                <form>
                    <h1>
                        Sign In
                    </h1>
                    <input required type="email" placeholder="Email or Phone Number" />
                    <input required type="password" placeholder="Password" />
                    <button className="loginButton">
                        <NavLink exact to="/" style={{ textDecoration: "none", color: "white" }}>
                            Sign In
                        </NavLink>
                    </button>
                    <span>New to Netflix?<b> <NavLink exact to="/register" style={{ textDecoration: "none", color: "white" }}>
                        Sign up now
                    </NavLink> .</b></span>
                    <small>
                        This page is protected by Google reCAPTHA to ensure you're not a bot. <b>Learn more</b>.
                    </small>
                </form>
            </div>
        </div>
    )
}

export default Login
